/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  background: #FFF;
  font-size: 1rem;
  line-height: 1.4;
  font-family: 'Roboto', sans-serif;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-weight: bold;
}

.logo__box {
  background: #032c9e;
  padding: 12px;
  margin-right: 10px;
}

.socials {
  height: 100%;
  display: flex;
  align-items: center;
}

.socials__link {
  display: block;
  width: 1rem;
}

.socials__link_facebook {
  background-image: url("../img/sprite.png");
  width: 16px;
  height: 16px;
  background-position: 0px 0px;
}

.socials__link_twitter {
  background-image: url("../img/sprite.png");
  width: 16px;
  height: 14px;
  background-position: 0px -26px;
}

.socials__link_instagram {
  background-image: url("../img/sprite.png");
  width: 16px;
  height: 16px;
  background-position: -26px 0px;
}

.socials__link_youtube {
  background-image: url("../img/sprite.png");
  width: 16px;
  height: 12px;
  background-position: -26px -26px;
}

.socials__link + .socials__link {
  margin-left: 30px;
}

.hero {
  position: relative;
  padding: 300px;
  min-height: 100vh;
  color: #fff;
  text-align: center;
  font-family: 'Barlow', sans-serif;
  margin-bottom: 100px;
}

.hero__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 85%);
  background: #032c9e url("../img/bg2.jpg") no-repeat 50% 50%;
  background-size: cover;
}

.hero__bg:after {
  content: '';
  background-color: #032c9e;
  opacity: 0.5;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero__inner {
  position: relative;
  max-width: 830px;
  margin: auto;
}

.hero__title {
  font-size: 60px;
  font-weight: bold;
  margin-bottom: 175px;
}

.chelik {
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
}

.chelik__photo {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 30px;
}

.out {
  padding-bottom: 200px;
}

.latest-news__head {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 100px;
}

.latest-news__title {
  font-size: 60px;
  font-weight: bold;
  color: #1b2550;
}

.latest-news__arrows .slick-prev, .latest-news__arrows .slick-next {
  background-color: transparent;
  width: 50px;
  height: 30px;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 50%;
}

.latest-news__arrows .slick-prev {
  background-image: url(../img/48px-tail-left.svg);
}

.latest-news__arrows .slick-next {
  background-image: url(../img/48px-tail-right.svg);
}

.slider__item {
  margin-right: 130px;
}

.row {
  display: flex;
  justify-content: space-between;
}

.col-left {
  width: 260px;
  margin-right: auto;
}

.col-right {
  width: 780px;
  margin-left: auto;
}

h2 {
  font-size: 48px;
  margin-bottom: 0.5em;
  color: #1b2550;
}

.text {
  color: #7c8092;
}

.video {
  margin: 70px 0;
}

.content {
  margin-bottom: 100px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  color: #ffffff;
  font-size: 16px;
  padding: 50px;
}

.header a {
  color: inherit;
  text-decoration: none;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__right {
  display: flex;
}

.header__logo {
  text-transform: uppercase;
}

.header__nav {
  display: flex;
  margin-right: 60px;
}

.header__nav a {
  font-family: 'Barlow', sans-serif;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  margin-right: 30px;
}

.header__nav a:last-child {
  margin-right: 0;
}

.header__nav a.line-th {
  text-decoration: line-through;
}

.block-news {
  display: block;
  max-width: 520px;
  font-family: 'Barlow', sans-serif;
  color: #303342;
}

.block-news__img {
  display: block;
  width: 540px;
  height: 360px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin-bottom: 70px;
  text-decoration: none;
}

.block-news_small .block-news__img {
  width: 100%;
  height: auto;
  padding-bottom: calc(360/540*100%);
  margin-bottom: 50px;
}

.block-news__title {
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 25px;
}

.block-news__date, .block-news__type {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 16px;
  color: #484b59;
}

.block-news__info {
  margin: 45px 0;
  color: #7c8092;
  line-height: 30px;
}

.block-news__link {
  display: block;
  position: relative;
  color: #1b2550;
  text-decoration: none;
  padding-left: 34px;
  line-height: 18px;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
}

.block-news__link:before {
  content: '';
  display: block;
  height: 100%;
  width: 4px;
  background-color: #1b2550;
  position: absolute;
  top: 0;
  left: 0;
}

.block-news__more {
  display: none;
}
